export default {
  name: 'STableList',
  props: {
    twoColumns: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      largeContainer: true
    }
  },
  mounted () {
    this.resizeHandle()
  },
  methods: {
    resizeHandle () {
      this.largeContainer = this.$refs.list.$el.clientWidth > 600
    }
  }
}
